*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Outfit", Sans-serif;
}
a,span{
    display: inline-block;
}
img{
    display: block;
}
.container{
    margin: 0 auto;
    position: relative;
}
.preloader{
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #000;
    transition-duration: 0.7s;
    pointer-events: none;
}
@media (max-width: 576px) { 
    .container{
        max-width: 100%;
    }
 }

@media (min-width: 576px) { 
    .container{
        max-width: 540px;
    }
 }


@media (min-width: 768px) {  
    .container{
        max-width: 720px;
    }
}


@media (min-width: 992px) { 
    .container{
        max-width: 960px;
    }
 }


@media (min-width: 1200px) { 
    .container{
        max-width: 1140px;
    }
 }


@media (min-width: 1400px) { 
    .container{
        max-width: 1320px;
    }
 }


.nav-main{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
}
.nav-main button{
    background: transparent;
    border: none;
    cursor: pointer;
}

/*=============================================
=            Hero main            =
=============================================*/

.hero-main{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 100vh;
    scroll-snap-align:center;
}
.hero-inr{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
}

.hero-texts{
    margin-right: 20px;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 94px;
    overflow: hidden;
    position: relative;
   
}

